import React, { Component } from "react";
import { Link } from "react-router-dom";
import NewCourse from "../../../components/NewCourse";

class NotFoundScreen extends Component {
    render() {
        return (
            <div>
                <div className="d-flex justify-content-center align-items-center" style={{
                    height: "calc(100vh)",
                    backgroundSize: "cover",
                    background: "#0c0c0c",
                }}>
                    <div style={{
                        textAlign: "center",
                    }}>
                        <h1 style={{ color: "white", fontWeight: "bolder", fontFamily: "inherit", fontSize: 120 }}>404</h1>
                        <h2 style={{
                            color: "white",
                            fontSize: 32,
                            fontFamily: "inherit",
                            letterSpacing: "-1px"
                        }}>Bu Sayfa Maalesef Bulunamadı</h2>
                        <div style={{
                            marginTop: 40
                        }}>
                            <Link to="/" style={{
                                outline: "none",
                                border: "none",
                                backgroundColor: "white",
                                color: "black",
                                fontSize: 20,
                                textDecoration: "none",
                                letterSpacing: -0.1,
                                height: "50px",
                                borderRadius: "25px",
                                textAlign: "center",
                                padding: "5px 25px",
                                fontWeight: "bold",
                                fontFamily: "inherit"
                            }}>Ana Sayfaya Git</Link>
                        </div>
                    </div>
                </div>
                <NewCourse />
            </div>
        );
    }
}

export default NotFoundScreen;