import React, { Component } from "react";
import CoursesData from '../../data/courses.js';
import StarValue from "../StarValue/index.js";
class Courses extends Component {
    renderOneCourse(item = {}) {
        return (
            <div onClick={() => {
                window.open(item?.url, "__blank")
            }} className="col-lg-4 col-md-6 col-12 justify-content-center d-flex align-items-center" style={{
                cursor: "pointer",
                maxWidth: "400px"
            }}>
                <div className="card courses-card">
                    <img src={item?.thumbnail || "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"} onError={(e) => {
                        e.target.src = "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"
                    }} className="card-img-top" width="100%" height="auto" style={{
                        objectFit: "cover"
                    }} alt="..." />
                    <div className="card-body courses-body">
                        <h1>{item?.title}</h1>
                        <p className="course-author"
                            onClick={() => {
                                window.open(item?.authorUrl, "__blank")
                            }}>
                            {item?.author}
                        </p>
                        <p className="course-stars">{item?.star} <span>{<StarValue value={item?.starValue} />}</span></p>
                        <p className="course-information">{item?.details}</p>
                        <p className="course-price">{item?.price}</p>
                        <p className="course-registered">{item?.registered} kayıt</p>

                    </div>
                </div>
            </div>
        )
    }
    renderCoursePoint(item = {
        url: "https://www.udemy.com/user/cangokceaslan",
        thumbnail: "assets/jpg/course-udemy-resized.jpg",
        title: "Daha Fazla Eğitime Ulaşmak İçin Hemen Ziyaret Et",
        author: "Can Gökçeaslan",
        authorUrl: "https://www.udemy.com/cangokceaslan",
        star: "4.3",
        starValue: 4.3,
        details: "Toplam 8,5 saat - 2 Kurs - Başlangıç",
        price: "Ücretsiz",
        registered: "41.000+"
    }) {
        return (
            <div onClick={() => {
                window.open("https://www.udemy.com/user/cangokceaslan", "__blank")
            }} className="col-lg-4 col-md-6 col-12 justify-content-center d-flex" style={{
                cursor: "pointer"
            }}>
                <div className="card courses-card">
                    <img src={item?.thumbnail} onError={(e) => {
                        e.target.src = "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"
                    }} className="card-img-top" width="100%" height="auto" style={{
                        objectFit: "cover"
                    }} alt="..." />
                    <div className="card-body courses-body">
                        <h1>{item?.title}</h1>
                        <p className="course-author"
                            onClick={() => {
                                window.open(item?.authorUrl, "__blank")
                            }}>
                            {item?.author}
                        </p>
                        <p className="course-stars">{item?.star} <span>{<StarValue value={item?.starValue} />}</span></p>
                        <p className="course-information">{item?.details}</p>
                        <p className="course-price">{item?.price}</p>
                        <p className="course-registered">{item?.registered} kayıt</p>
                    </div>
                </div>
            </div>
        )
    }
    render() {
        return (
            <section className="courses" id="courses">
                <h2 className="courses-header">
                    <img src="assets/svg/udemy.svg" width="120px" height="40px" style={{ marginRight: "5px" }} />Eğitimlerim
                </h2>
                <div className="container-fluid top-container">
                    <div className="courses-container">
                        <div className="container" style={{ maxWidth: "1300px" }}>
                            <div className="row justify-content-center d-flex align-items-center">
                                {CoursesData.map((course, index) => {
                                    return this.renderOneCourse(course)
                                })}
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}

export default Courses;