                                    <p>Kişisel merakım ve araştırma hevesim her seferinde beni rahatsız edecek bir konu buluyor. Keşfettiğim şeyleri insanlarla paylaşmak benim için bir hobi. Bu hobiyi gerçekleştirebilmek için bir yandan <a target="_blank" href="https://www.cangokceaslan.com/sosyal"><strong>içerik üreticiliği</strong></a> yapıyorum. Özellikle <a target="_blank" href="https://www.udemy.com/user/cangokceaslan"><strong>Udemy</strong></a>  üzerinde paylaştığım kurslar sayesinde toplamda 55 bin kişilik bir kitlem bulunuyor. Bunu günün birinde tüm dünyadaki teknoloji ve yazılım ile ilgilenen bir kitleye yapabilmek benim yol haritamda ulaşmak istediğim en önemli noktalardan birisidir.</p>
