import { withRouter } from "react-router";
import React from "react";
const courseData = {
    nodejs: "https://www.udemy.com/course/nodejs-egitimi-sifirdan-profesyonele/?referralCode=0017AFE73990096201B4",
    javascript: "https://www.udemy.com/course/javascript-temelden-profesyonele-kurs-1/",
    github: "https://www.udemy.com/course/github-egitimi/"
}
class CourseLoader extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loaded: false,
        };
    }

    componentDidMount() {
        const key = this.props.match.params.key;
        if (courseData?.[key] !== undefined) {
            window.location.href = courseData[key];
        }
        this.setState({ loaded: true });
    }

    render() {
        if (!this.state.loaded) {
            return <div>Loading...</div>;
        }
        return (
            <div style={{
                width: "100vw",
                height: "calc(100vh - 300px)",
                display: "flex",
                justifyContent: "center",
                paddingTop: "70px",
                backgroundColor: "#2D2D2D"
            }}>
                <div className="d-flex justify-content-center align-items-center">
                    <div class="spinner-border text-light" style={{
                        width: "8rem",
                        height: "8rem",
                        borderWidth: "12px"
                    }} role="status">
                        <span class="visually-hidden">Loading...</span>
                    </div>
                </div>
            </div>
        )
    }
}
export default withRouter(CourseLoader);